/* Responsive Booking Table Styles */

.responsive-booking-container {
  width: 100%;
  min-height: 200px;
  position: relative;
}

/* Debug helper - remove in production */
.responsive-booking-container::before {
  /* content: "Mobile View Active - Cards: " attr(data-card-count); */
  position: absolute;
  top: -20px;
  right: 0;
  font-size: 11px;
  color: #666;
  background: #f0f0f0;
  padding: 2px 6px;
  border-radius: 3px;
  z-index: 10;
}

/* @media (min-width: 1200px) {
  .responsive-booking-container::before {
    content: "Desktop View Active - Cards: " attr(data-card-count);
  }
} */

/* Additional debug styles */
.mobile-cards-wrapper:empty::after {
  content: "No booking data available";
  display: block;
  padding: 20px;
  text-align: center;
  color: #666;
  font-style: italic;
  border: 2px dashed #ddd;
  border-radius: 8px;
}

/* Removed debug border */

/* Mobile Cards Styles */
.mobile-cards-wrapper {
  display: flex !important;
  flex-direction: column;
  gap: 16px;
  width: 100%;
  min-height: 100px; /* Ensure minimum height for visibility */
  opacity: 1 !important;
  visibility: visible !important;
  position: relative;
  z-index: 1;
  /* background: rgba(
    255,
    0,
    0,
    0.1
  ); */
   /* Temporary red background to see container */
  /* border: 2px solid red;  */
  /* Temporary border to see container bounds */
  padding: 10px;
}

/* Force visibility on smaller screens */
@media (max-width: 1199px) {
  .responsive-booking-container .mobile-cards-wrapper {
    display: flex !important;
    visibility: visible !important;
    opacity: 1 !important;
    position: relative !important;
    z-index: 1 !important;
  }

  .responsive-booking-container .desktop-table-wrapper {
    display: none !important;
    visibility: hidden !important;
  }
}

.booking-card {
  background: #ffffff;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  animation: slideInUp 0.4s ease-out forwards;
  animation-delay: calc(var(--index) * 0.1s);
  opacity: 1; /* Changed from 0 to 1 for immediate visibility */
  transform: translateY(0); /* Changed from translateY(20px) to 0 */
  margin-bottom: 16px;
  display: block !important;
  visibility: visible !important;
  /* background: rgba(0, 255, 0, 0.1) !important;  */
  /* Temporary green background */
  /* border: 2px solid green !important;  */
  /* Temporary border */
  min-height: 200px; /* Ensure minimum height */
}

.booking-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
  transform: translateY(-2px);
}

/* Card Header */
.booking-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #e2e8f0;
}

.booking-id-section {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.booking-id-label {
  font-size: 12px;
  color: #64748b;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.booking-id-value {
  font-size: 16px;
  font-weight: 700;
  color: #1e293b;
}

.download-btn-mobile {
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 16px;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 2px 4px rgba(59, 130, 246, 0.3);
}

.download-btn-mobile:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(59, 130, 246, 0.4);
}

.download-btn-mobile:active {
  transform: translateY(0);
}

/* Card Body */
.booking-card-body {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.service-name-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.service-name {
  font-size: 18px;
  font-weight: 600;
  color: #1e293b;
  margin: 0;
  line-height: 1.3;
}

.service-type-badge {
  display: inline-block;
  background: linear-gradient(135deg, #f0f9ff, #e0f2fe);
  color: #0369a1;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border: 1px solid #bae6fd;
  width: fit-content;
}

/* Details Grid */
.booking-details-grid {
  display: grid;
  gap: 16px;
}

.detail-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.detail-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background: linear-gradient(135deg, #f8fafc, #e2e8f0);
  border-radius: 8px;
  color: #64748b;
  font-size: 14px;
  flex-shrink: 0;
  margin-top: 2px;
}

.detail-content {
  display: flex;
  flex-direction: column;
  gap: 2px;
  flex: 1;
}

.detail-label {
  font-size: 12px;
  color: #64748b;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.detail-value {
  font-size: 14px;
  color: #1e293b;
  font-weight: 500;
  line-height: 1.4;
}

/* Price Item Special Styling */
.price-item {
  background: linear-gradient(135deg, #f0fdf4, #dcfce7);
  border: 1px solid #bbf7d0;
  border-radius: 8px;
  padding: 12px;
  margin-top: 8px;
}

.price-content {
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}

.price-value {
  font-size: 18px;
  font-weight: 700;
  color: #15803d;
}

/* Total Section */
.total-section {
  margin-top: 24px;
  padding-top: 20px;
  border-top: 2px solid #e2e8f0;
}

.total-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #fafafa, #f4f4f5);
  padding: 16px 20px;
  border-radius: 12px;
  border: 1px solid #e2e8f0;
}

.total-label {
  font-size: 16px;
  font-weight: 600;
  color: #1e293b;
}

.total-value {
  font-size: 20px;
  font-weight: 700;
  color: #15803d;
}

/* Desktop Table Enhancements */
.desktop-table-wrapper {
  display: none; /* Hidden by default */
}

@media (min-width: 1200px) {
  .desktop-table-wrapper {
    display: block !important;
    visibility: visible !important;
  }

  .mobile-cards-wrapper {
    display: none !important;
    visibility: hidden !important;
  }
}

.desktop-table-wrapper .list-table {
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
}

.desktop-table-wrapper .list-row {
  transition: background-color 0.2s ease;
  padding: 16px 8px;
}

.desktop-table-wrapper .list-row.header {
  background: linear-gradient(135deg, #f8fafc, #f1f5f9);
  font-weight: 600;
  color: #475569;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.desktop-table-wrapper .list-row:not(.header) {
  border-bottom: 1px solid #f1f5f9;
}

.desktop-table-wrapper .list-row:not(.header):hover {
  background: #f8fafc;
}

.desktop-table-wrapper .list-row .col {
  padding: 8px 12px;
  font-size: 14px;
  color: #1e293b;
}

.download-btn {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
  color: white;
  border: none;
  border-radius: 6px;
  padding: 8px 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
}

.download-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(59, 130, 246, 0.3);
}

/* Animations */
@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile First Responsive Design */
@media (max-width: 575px) {
  .mobile-cards-wrapper {
    padding: 0 8px;
  }

  .booking-card {
    padding: 16px;
    margin-bottom: 12px;
  }

  .booking-card-header {
    flex-direction: column;
    gap: 12px;
    align-items: stretch;
  }

  .download-btn-mobile {
    align-self: flex-end;
    width: fit-content;
  }

  .booking-details-grid {
    grid-template-columns: 1fr;
    gap: 12px;
  }
}

/* Tablet Responsive */
@media (min-width: 576px) and (max-width: 1199px) {
  .mobile-cards-wrapper {
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
  }

  .desktop-table-wrapper {
    display: none !important;
  }

  .booking-details-grid {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  .booking-card {
    padding: 24px;
    max-width: 100%;
    width: 100%;
  }

  .service-name {
    font-size: 20px;
  }

  .price-item {
    grid-column: 1 / -1;
  }

  .booking-card-header {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}

/* Large Tablet/Small Desktop */
@media (min-width: 992px) and (max-width: 1199px) {
  .mobile-cards-wrapper {
    display: block !important;
  }

  .desktop-table-wrapper {
    display: none !important;
  }

  .booking-details-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
  }

  .booking-card {
    padding: 28px;
  }
}

/* Dark Mode Support */
@media (prefers-color-scheme: dark) {
  .booking-card {
    background: #1e293b;
    border-color: #334155;
    color: #f1f5f9;
  }

  .booking-card-header {
    border-bottom-color: #334155;
  }

  .booking-id-value,
  .service-name,
  .detail-value {
    color: #f1f5f9;
  }

  .booking-id-label,
  .detail-label {
    color: #94a3b8;
  }

  .detail-icon {
    background: linear-gradient(135deg, #334155, #475569);
    color: #94a3b8;
  }

  .price-item {
    background: linear-gradient(135deg, #064e3b, #065f46);
    border-color: #059669;
  }

  .total-row {
    background: linear-gradient(135deg, #334155, #475569);
    border-color: #64748b;
  }

  .total-label {
    color: #f1f5f9;
  }
}

/* Accessibility */
.download-btn:focus,
.download-btn-mobile:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

.booking-card:focus-within {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Print Styles */
@media print {
  .download-btn,
  .download-btn-mobile {
    display: none;
  }

  .booking-card {
    box-shadow: none;
    border: 1px solid #000;
    break-inside: avoid;
    margin-bottom: 20px;
  }

  .total-value,
  .price-value {
    color: #000 !important;
  }
}

/* Loading State */
.booking-card.loading {
  opacity: 0.6;
  pointer-events: none;
}

.booking-card.loading::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid #f3f3f3;
  border-top: 2px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
