.list-table {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.list-row {
  display: flex;
  flex-wrap: nowrap;
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}

.list-row.header {
  font-weight: 700;
  background-color: #f8f9fa;
  border-radius: 5px;
}

.list-row .col {
  flex: 1;
  padding: 5px;
  font-size: 16px;
}




